.cd-container {
    margin-left: 85px;
    position: relative;
    z-index: -1;
  }

  .App-logo {
    animation: App-logo-spin infinite 9s linear;
    height: 120px;
    pointer-events: none;
    min-height: 85px;
  }

  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }